const getAccessToken = async() => {
    var data = localStorage.getItem('1986authdata19901985dataprocess_autoconcept_vo_access_token_info');
    if(data!=null){
        var tempData = JSON.parse(data);
        return tempData.access_token;
    }
    return null;
}

const isAuthorized = () => {
    var data = localStorage.getItem('1986authdata19901985dataprocess_autoconcept_vo_access_token_info');
    if(data!=null){
        var tempData = JSON.parse(data);
        if (new Date(tempData[".expires"]).getTime() < new Date().getTime()) {
            signOut();
            return false;
        }else{
          return true;
        }
    }
  return false;
}

const signOut = () => {
    localStorage.removeItem('1986authdata19901985dataprocess_autoconcept_vo_access_token_info');
}

const clearSessionData = () => {
    localStorage.removeItem('vo-info');
    localStorage.removeItem('currentCoverage');
    localStorage.removeItem('vo-details');
    localStorage.removeItem('DibsData');
    localStorage.removeItem('autoconcept_vo_products');
}

const login = async (data) => {
    const params = new URLSearchParams();
    params.append('username', data.username);
    params.append('password', data.password);
    params.append('grant_type', data.grant_type);
    params.append('client_id', data.client_id);
    params.append('culture_code', data.culture_code);
    params.append('user_type', data.user_type);
    return HttpService.postLoginAsync(URL + 'Token', params, config);
};

const loginNewCustomer = async (data, isnewuser) => {
    const params = new URLSearchParams();
    params.append('username', data.username);
    params.append('password', data.password);
    params.append('grant_type', data.grant_type);
    params.append('client_id', data.client_id);
    params.append('culture_code', data.culture_code);
    params.append('user_type', data.user_type);
    if(isnewuser){
        params.append('is_otp_user', true);
    }
    return HttpService.postLoginAsync(URL + 'Token', params, config);
};

const resetPassword = async (data) => {
    return HttpService.postAsync(URL + 'api/WebsiteAuth/ResetLogin', data);
};

const dealerResetPassword = async (data) => {
    return HttpService.postAsync(URL + 'api/WebsiteAuth/DealerResetLogin', data);
};

const ChangePassword = async (data) => {
    return HttpService.postAsync(URL + 'api/WebsiteAuth/ChangePasswordRequest', data);
};

const AcceptPolicy = async () => {
    return HttpService.postAsync(URL + 'api/WebsiteAuth/UpdatePolicyAccepted?status='+true);
};

const resendOtpCode = async (data) => {
    return HttpService.postAsync(URL + 'api/WebsiteAuth/ResendOtpCode', data);
};

const UpdateNewPassword = async (data, token) => {
    const config = {
        headers: {
            'Authorization': "bearer " + token
        }
      }
    return HttpService.postLoginAsync(URL + 'api/WebsiteAuth/UpdatePassword', data, config);
};


export default {login,getIsCompany,getLoginFlow, getTokenInfo, getAccessToken, storeTokenInfo, isAuthorized, signOut, resetPassword,dealerResetPassword, AcceptPolicy, ChangePassword, getCultureCode, getLangCode, matchCultureAndRedirectURL,IsUrlMatchWithCultureCode, getCurrencyCode, getDibsImageNets, getDibsImageVisa, getPaymentUpdateImage, loginNewCustomer, resendOtpCode,clearSessionData, UpdateNewPassword };