import React, { useEffect } from 'react';
import '../../styles/App.scss';
import { useGlobalStore } from '../../utils/global-store/vo/Vo-globalStore';
import * as Sentry from '@sentry/react';

import HttpService from '../../services/shared/http-service';
import LoginPage from '../../components/dealer/login-page/LoginPage';

const DealerLoginApp = ({ moduleData, moduleName }) => {
  const [globalVariable, globalVariabledispatch] = useGlobalStore(false);
  globalVariabledispatch('SET_MODULE_DATA', { moduleData: moduleData });

  return (
    <div>
      <LoginPage></LoginPage>
    </div>
  );
};

//export default VoLoginApp;
export default Sentry.withProfiler(DealerLoginApp);
