import React, { useEffect, useState, useRef } from 'react';
import * as Sentry from '@sentry/react';
import { useForm } from 'react-hook-form';
import PasswordChange from '../../vo/shared-components/password-change/PasswordChange';
import PasswordResetPage from '../../vo/password-reset-page/PasswordResetPage';
import Loading from '../../vo/shared-components/loading/Loading';

import AuthService from '../../../services/vo/auth-service';
import Consent from './consent/consent';
//global states
import { useGlobalStore } from '../../../utils/global-store/vo/Vo-globalStore';

const LoginPage = () => {
  const [globalVariable] = useGlobalStore(false);
  const [loading, SetLoading] = useState(false);
  const [isLogin, SetIsLogin] = useState(true);
  const [isFirstTimePasswordChanged, SetIsFirstTimePasswordChanged] = useState(
    false,
  );
  const [isPolicyAccepted, SetIsPolicyAccepted] = useState(false);
  const [isPasswordReset, SetIsPasswordReset] = useState(false);
  const [tokenData, SetTokenData] = useState(null);
  const { handleSubmit, register, errors, setValue } = useForm();
  const [errorMessage, SetErrorMesssage] = useState(null);
  const [userNameToolTipVisible, SetUserNameToolTipVisible] = useState(false);
  const [passwordToolTipVisible, SetPasswordToolTipVisible] = useState(false);
  const [isDealerLogin, SetIsDealerLogin] = useState(true);

  const userNameToolTipWrapper = useRef(null);
  const passwordToolTipWrapper = useRef(null);

  const handleClickOutside = event => {
    if (
      userNameToolTipWrapper.current &&
      !userNameToolTipWrapper.current.contains(event.target)
    ) {
      SetUserNameToolTipVisible(false);
    }
    if (
      passwordToolTipWrapper.current &&
      !passwordToolTipWrapper.current.contains(event.target)
    ) {
      SetPasswordToolTipVisible(false);
    }
  };

  useEffect(() => {
    document.addEventListener('click', handleClickOutside, false);
    CheckAuthFlow();
    return () => {
      document.removeEventListener('click', handleClickOutside, false);
    };
  }, []);

  const CheckAuthFlow = () => {
    SetLoading(true);
    const isAuthenticated = AuthService.isAuthorized();
    if (isAuthenticated) {
      const currentToken = AuthService.getTokenInfo();
      HandlePageRedirect(currentToken);
    } else {
      Sentry.configureScope(scope => scope.setUser(null));
      SetLoading(false);
    }
  };

  const HandlePageRedirect = token => {
    if (
      token?.cultureCode?.toLowerCase() !==
      globalVariable.moduleData.language.toLowerCase()
    ) {
      AuthService.signOut();
      SetLoading(false);
    } else {
      SetLoading(false);
      window.location.assign(globalVariable.moduleData.home_page_url);
    }
  };

  
      const HandlePageRedirect1 = (token) => {
          if ((token?.isFirstTimePasswordChanged == "True") && (token?.isPolicyAccepted == "True")) {
              // if (token?.isValidCustomerInfo == "False") {
              //     window.location.assign(globalVariable.moduleData.profile_page_url + '?missed_data=' + btoa(true));
              // } else {
              //     window.location.assign(globalVariable.moduleData.home_page_url);
              // }
              window.location.assign(globalVariable.moduleData.home_page_url);
          }
          else {
              SetIsPolicyAccepted(token.isPolicyAccepted == "True")
              SetTokenData(token);
              SetIsFirstTimePasswordChanged(token.isFirstTimePasswordChanged == "True");
              SetIsLogin(false);
              SetLoading(false);
          }
      }

  const OnPasswordResetClicked = () => {
    SetErrorMesssage(null);
    SetIsLogin(false);
    SetIsPasswordReset(true);
  };

  const GoToLoginPage = username => {
    SetIsPasswordReset(false);
    SetIsLogin(true);
    setValue('username', username, { shouldValidate: true });
  };

  const onPolicyAccepted = () => {
    tokenData.isPolicyAccepted = 'True';
    AuthService.storeTokenInfo(tokenData);
    SetIsPolicyAccepted(true);
    CheckAuthFlow();
  };

  const onPasswordChange = () => {
    tokenData.isFirstTimePasswordChanged = 'True';
    AuthService.storeTokenInfo(tokenData);
    SetIsFirstTimePasswordChanged(true);
    CheckAuthFlow();
  };

  const onSubmit = async values => {
    debugger;
    SetErrorMesssage(null);
    try {
      SetLoading(true);
      const data = {
        username: values.username.trim(),
        password: values.password,
        grant_type: 'password',
        client_id: 'AutoconceptWeb',
        culture_code: globalVariable.moduleData.language,
        user_type: 2,
      };
      const response = await AuthService.login(data);
      response.data.cultureCode = globalVariable.moduleData.language;
      response.data.loginflow = 'Main';
      if (response.data.userTypeId == 2) {
        await AuthService.storeTokenInfo(response.data);
       debugger;
        HandlePageRedirect1(response.data);
        //  window.location.assign(globalVariable.moduleData.home_page_url);
      } else {
        SetIsDealerLogin(false);
      }
      SetLoading(false);
    } catch (error) {
      SetLoading(false);
      if (error != null) {
        SetErrorMesssage(globalVariable.moduleData.error_message);
      }
    }
  };

  return (
    <section className="login-container mid-container">
      {loading ? (
        <Loading></Loading>
      ) : (
        <div>
          <div class="flex mob-block wrap">
            {isLogin ? (
              <div class="mt-30 width-50">
                <form onSubmit={handleSubmit(onSubmit)}>
                  <h5>{globalVariable.moduleData.login_panal_title}</h5>
                  <div class="flex column mb-30">
                    <label class="input-label mb-5">
                      {globalVariable.moduleData.login_username}
                      <span className="ToolTips">
                        <span
                          className="tooltip-icon"
                          ref={userNameToolTipWrapper}
                          onClick={() =>
                            SetUserNameToolTipVisible(!userNameToolTipVisible)
                          }
                        ></span>
                        {userNameToolTipVisible ? (
                          <span className="tooltip-box">
                            {
                              globalVariable.moduleData
                                .login_username_tooltip_text
                            }
                          </span>
                        ) : (
                          ''
                        )}
                      </span>
                    </label>
                    <input
                      class="input-style-2"
                      type="text"
                      name="username"
                      ref={register({
                        required:
                          globalVariable.moduleData.login_username_error,
                      })}
                    />
                    <span className="error">
                      {' '}
                      {errors.username && errors.username.message}
                    </span>
                  </div>
                  <div class="flex column mb-30">
                    <label class="input-label mb-5">
                      {globalVariable.moduleData.login_password}
                      <span className="ToolTips">
                        <span
                          className="tooltip-icon"
                          ref={passwordToolTipWrapper}
                          onClick={() =>
                            SetPasswordToolTipVisible(!passwordToolTipVisible)
                          }
                        ></span>
                        {passwordToolTipVisible ? (
                          <span className="tooltip-box">
                            {
                              globalVariable.moduleData
                                .login_password_tooltip_text
                            }
                          </span>
                        ) : (
                          ''
                        )}
                      </span>
                    </label>
                    <input
                      class="input-style-2"
                      type="password"
                      name="password"
                      ref={register({
                        required:
                          globalVariable.moduleData.login_password_error,
                      })}
                    />
                    <span className="error">
                      {' '}
                      {errors.password && errors.password.message}
                    </span>
                  </div>
                  {errorMessage != null ? (
                    <div class="flex column">
                      <span className="error"> {errorMessage}</span>
                    </div>
                  ) : (
                    ''
                  )}

                  {!isDealerLogin && (
                    <div class="flex column">
                      <span className="error">
                        {globalVariable.moduleData.dealer_login_error_msg}
                      </span>
                    </div>
                  )}

                  <div class="flex mb-30 v-center space-between">
                    <div>
                      <a onClick={OnPasswordResetClicked}>
                        {globalVariable.moduleData.forgot_password}
                      </a>
                    </div>
                    <button type="submit" className="btn btn-primary">
                      {globalVariable.moduleData.sign_in_test}
                    </button>
                  </div>
                </form>
              </div>
            ) : isPasswordReset ? (
              <PasswordResetPage
                GoToLoginPage={GoToLoginPage}
              ></PasswordResetPage>
            ) : !isPolicyAccepted ? (
              <Consent
                onPolicyAccepted={onPolicyAccepted}
                tokenData={tokenData}
              ></Consent>
            ) : !isFirstTimePasswordChanged ? (
              <PasswordChange
                onPasswordChange={onPasswordChange}
                tokenData={tokenData}
                isLoginFlow={true}
              ></PasswordChange>
            ) : (
              ''
            )}
          </div>
        </div>
      )}
    </section>
  );
};

export default LoginPage;
